  computed: {
    currentUserId() {
      //  localStorage 
      return localStorage.getItem('currentUserId')
    },
    filteredChannels() {
      if (!this.searchQuery) return this.channels;
      
      const query = this.searchQuery.toLowerCase();
      return this.channels.filter(channel => {
        const searchableValues = {
          id: channel.id,
          type: channel.type,
          name: channel.name,
          identifier: channel.identifier,
          status: channel.status
        };
        
        return Object.values(searchableValues)
          .some(value => String(value).toLowerCase().includes(query));
      });
    },
    selectedProvinces() {
      return this.provinces.filter(province => 
        this.currentChannel.regionBlacklist.provinces.includes(province.value)
      );
    },
    hasBlockedRegions() {
      return this.currentChannel.regionBlacklist.provinces.length > 0 || this.currentChannel.regionBlacklist.cities.length > 0;
    },
    availableProvinces() {
      // 
      return this.provinces;
    },
    ...mapState(useDomainStore, ['domains'])
  },
  watch: {
    'currentChannel.regionBlacklist.enabled'(newVal) {
      if (newVal && this.regionRules.length === 0) {
        this.addRule();
      }
    }
  },
  methods: {
    ...mapActions(useDomainStore, ['fetchDomains']),
    async loadChannels() {
      try {
        this.loading = true;
        this.error = null;
        
        const response = await this.$api.get('/api/channels');
        
        this.channels = response.data.map(channel => {
          // 
          const processedChannel = {
            ...channel,
            customParams: channel.customParams || {
              userId: '',
              name: channel.name,
              identifier: channel.identifier
            },
            announcement: channel.announcement || {
              enabled: false,
              content: ''
            },
            shareBlacklist: channel.shareBlacklist || {
              enabled: false,
              excludedUsers: []
            },
            regionBlacklist: channel.regionBlacklist || {
              enabled: false,
              provinces: [],
              cities: []
            },
            chatFeature: channel.chatFeature || {
              enabled: false
            }
          };

          return processedChannel;
        });
        
      } catch (error) {
        console.error('Load channels error:', error);
        this.error = error.response?.data?.message || '';
      } finally {
        this.loading = false;
      }
    },
    async generateQR(channel) {
      try {
        // ID
        const userId = localStorage.getItem('currentUserId');
        if (!userId) {
          this.$message.error('');
          return;
        }

        // --  --
        if (!this.selectedDomain && this.domains && this.domains.length > 0) {
          this.selectedDomain = this.domains[0].name;
        }

        // 
        if (!this.selectedDomain) {
          this.modalType = 'qr';
          this.currentChannel = { ...channel };
          this.showModal = true;
          this.modalMessage = '';
          this.qrCodeUrl = '';
          return;
        }

        //  http://
        const baseUrl = this.selectedDomain;
        const paymentUrl = `${baseUrl}/#/payment?userId=${encodeURIComponent(userId)}&channelId=${encodeURIComponent(channel.identifier)}`;

        // --  --
        // 
        this.currentChannel = { ...channel };
        this.modalType = 'qr';
        this.showModal = true;
        this.modalMessage = `: ${paymentUrl}`;
        
        // canvas
        const QRCode = await import('qrcode');
        const tempCanvas = document.createElement('canvas');
        tempCanvas.width = 200;
        tempCanvas.height = 200;
        
        // canvas
        await new Promise((resolve, reject) => {
          QRCode.toCanvas(tempCanvas, paymentUrl, {
            width: 200,
            margin: 0,
            errorCorrectionLevel: 'H',
            color: {
              dark: '#000000',
              light: '#ffffff'
            }
          }, (err) => {
            if (err) reject(err);
            else resolve();
          });
        });
        
        // 
        const templateImg = new Image();
        templateImg.crossOrigin = 'Anonymous';
        
        templateImg.onload = () => {
          try {
            const finalCanvas = document.createElement('canvas');
            const ctx = finalCanvas.getContext('2d');
            const maxWidth = 400;
            let newWidth = templateImg.width;
            let newHeight = templateImg.height;
            if (newWidth > maxWidth) {
              const ratio = maxWidth / newWidth;
              newWidth = maxWidth;
              newHeight = templateImg.height * ratio;
            }
            finalCanvas.width = newWidth;
            finalCanvas.height = newHeight;
            ctx.drawImage(templateImg, 0, 0, newWidth, newHeight);
            
            const whiteRectRelX = 0.17;
            const whiteRectRelY = 0.21;
            const whiteRectRelWidth = 0.65;
            const whiteRectRelHeight = 0.44;
            const whiteRectX = newWidth * whiteRectRelX;
            const whiteRectY = newHeight * whiteRectRelY;
            const whiteRectWidth = newWidth * whiteRectRelWidth;
            const whiteRectHeight = newHeight * whiteRectRelHeight;
            const qrSize = Math.min(whiteRectWidth, whiteRectHeight);
            const qrCodeX = whiteRectX + (whiteRectWidth - qrSize) / 2;
            const qrCodeY = whiteRectY + (whiteRectHeight - qrSize) / 2;
            
            ctx.drawImage(tempCanvas, qrCodeX, qrCodeY, qrSize, qrSize);
            
            ctx.shadowColor = 'transparent';
            ctx.shadowBlur = 0;
            ctx.shadowOffsetX = 0;
            ctx.shadowOffsetY = 0;
            
            this.qrCodeUrl = finalCanvas.toDataURL('image/png');
          } catch (err) {
            console.error('Canvas:', err);
            this.qrCodeUrl = tempCanvas.toDataURL('image/png');
          }
        };
        
        templateImg.onerror = () => {
          console.error('');
          this.qrCodeUrl = tempCanvas.toDataURL('image/png');
        };
        
        templateImg.src = process.env.NODE_ENV === 'development' 
          ? '/images/h8.png' 
          : `${process.env.BASE_URL}images/h8.png`;

      } catch (err) {
        console.error(':', err);
        this.$message.error('');
      }
    },
    openSettings(channel) {
      this.modalType = 'settings';
      this.currentChannel = JSON.parse(JSON.stringify(channel)); // 
      // 
      if (!this.currentChannel.customParams) this.$set(this.currentChannel, 'customParams', {});
      if (!this.currentChannel.announcement) this.$set(this.currentChannel, 'announcement', { enabled: false, content: '' });
      if (!this.currentChannel.shareBlacklist) this.$set(this.currentChannel, 'shareBlacklist', { enabled: false });
      if (!this.currentChannel.regionBlacklist) this.$set(this.currentChannel, 'regionBlacklist', { enabled: false, provinces: [], cities: [] });
      if (!this.currentChannel.chatFeature) this.$set(this.currentChannel, 'chatFeature', { enabled: false });
      if (!this.currentChannel.nameRequired) this.$set(this.currentChannel, 'nameRequired', { enabled: false });
      
      this.activeTab = 'basic'; // tab
      this.showModal = true;
      this.regionRules = []; // 
    },
    async saveSettings() {
      try {
        this.isSaving = true;
        
        const data = {
          status: this.currentChannel.status,
          customParams: this.currentChannel.customParams,
          announcement: this.currentChannel.announcement,
          shareBlacklist: this.currentChannel.shareBlacklist,
          regionBlacklist: this.currentChannel.regionBlacklist,
          chatFeature: this.currentChannel.chatFeature,
          nameRequired: this.currentChannel.nameRequired
        };
        
        
        await this.$api.put(
          `/api/channels/${this.currentChannel._id}/settings`, 
          data
        );

        this.$message.success('');
        this.closeModal(); // 
        this.loadChannels(); // 
      } catch (error) {
        this.$message.error(error.response?.data?.message || '');
      } finally {
        this.isSaving = false;
      }
    },
    closeModal() {
      this.showModal = false
      this.modalType = ''
      this.qrCodeUrl = ''
      this.modalMessage = ''
    },
    async toggleStatus(channel) {
      try {
        const newStatus = channel.status === '' ? '' : '';
        
        const settingsData = {
          status: newStatus,
          customParams: {
            ...channel.customParams,
            userId: channel.customParams?.userId || '',
            name: channel.name,
            identifier: channel.identifier
          }
        };

        const response = await this.$api.put(
          `/api/channels/${channel._id}/settings`, 
          settingsData
        );

        // 
        const updatedChannel = response.data.channel;
        const index = this.channels.findIndex(c => c._id === updatedChannel._id);
        if (index !== -1) {
          // Vue
          this.$set(this.channels, index, {
            ...this.channels[index],
            ...updatedChannel
          });
        }

        // 
        this.$message.success(`${newStatus}`);
      } catch (error) {
        // 
        this.$message.error('' + (error.response?.data?.message || ''));
        // 
        channel.status = channel.status === '' ? '' : '';
      }
    },
    checkMobile() {
      this.isMobile = window.innerWidth <= 768;
    },
    isPaymentChannel(channelName) {
      return channelName.includes('') || channelName.includes('');
    },
    initRegionRules() {
      this.regionRules = [];
      
      const { provinces, cities } = this.currentChannel.regionBlacklist;
      
      // 
      provinces.forEach(province => {
        const rule = {
          province,
          cities: cities.filter(city => 
            this.getCitiesByProvince(province).some(c => c.value === city)
          )
        };
        this.regionRules.push(rule);
      });

      // 
      if (this.regionRules.length === 0 && this.currentChannel.regionBlacklist.enabled) {
        this.regionRules.push({ province: '', cities: [] });
      }
      
      // 
      this.$forceUpdate();
    },
    addRule() {
      this.regionRules.push({
        province: '',
        cities: []
      });
      this.$nextTick(() => {
        this.syncRegionBlacklist();
      });
    },
    removeRule(index) {
      this.regionRules.splice(index, 1);
      this.syncRegionBlacklist();
    },
    handleProvinceChange(val, index) {
      // 
      this.regionRules[index].cities = [];
      // 
      this.$nextTick(() => {
        this.syncRegionBlacklist();
      });
    },
    getCitiesByProvince(province) {
      return this.cities[province] || [];
    },
    getProvinceLabel(value) {
      const province = this.provinces.find(p => p.value === value);
      return province ? province.label : value;
    },
    getCityLabel(cityValue, provinceValue) {
      if (!provinceValue || !cityValue) return '';
      
      const cityList = this.cities[provinceValue];
      if (!cityList) return cityValue;
      
      const city = cityList.find(c => c.value === cityValue);
      return city ? city.label : cityValue;
    },
    removeProvince(province) {
      // 
      const index = this.currentChannel.regionBlacklist.provinces.indexOf(province);
      if (index !== -1) {
        this.currentChannel.regionBlacklist.provinces.splice(index, 1);
        
        const removedCities = this.provinces
          .find(p => p.value === province)
          ?.cities.map(c => c.value) || [];
        
        this.currentChannel.regionBlacklist.cities = 
          this.currentChannel.regionBlacklist.cities
            .filter(city => !removedCities.includes(city));
      }
    },
    removeCity(city) {
      const index = this.currentChannel.regionBlacklist.cities.indexOf(city);
      if (index !== -1) {
        this.currentChannel.regionBlacklist.cities.splice(index, 1);
      }
    },
    // 
    convertRulesToBlacklist() {
      const provinces = [];
      const cities = [];
      
      this.regionRules.forEach(rule => {
        if (rule.province) {
          // 
          if (rule.cities && rule.cities.length > 0) {
            provinces.push(rule.province);  // 
            cities.push(...rule.cities);    // 
            // 
            if (['', '', '', ''].includes(rule.province)) {
              cities.push(rule.province);
            }
          } else {
            // 
            provinces.push(rule.province);
          }
        }
      });

      return {
        enabled: this.currentChannel.regionBlacklist.enabled,
        provinces: [...new Set(provinces)],
        cities: [...new Set(cities)]
      };
    },
    validateRegionRule(rule) {
      // ,
      if (rule.province && !rule.city) {
        //  Element UI 
        this.$notify({
          title: '',
          message: `${rule.province}`,
          type: 'warning',
          duration: 2000
        });
      }
    },
    handleShareBlacklistChange() {
      // 
      this.$forceUpdate(); // 
    },
    toggleViewMode() {
      this.viewMode = this.viewMode === 'card' ? 'table' : 'card';
      localStorage.setItem('channelViewMode', this.viewMode);
    },
    syncRegionBlacklist() {
      const provinces = [];
      const cities = [];
      
      this.regionRules.forEach(rule => {
        if (rule.province) {
          if (rule.cities && rule.cities.length > 0) {
            provinces.push(rule.province);  // 
            cities.push(...rule.cities);    // 
            // 
            if (['', '', '', ''].includes(rule.province)) {
              cities.push(rule.province);
            }
          } else {
            // 
            provinces.push(rule.province);
          }
        }
      });
      
      this.currentChannel.regionBlacklist = {
        enabled: this.currentChannel.regionBlacklist.enabled,
        provinces: [...new Set(provinces)],
        cities: [...new Set(cities)]
      };
    },
    findProvinceByCity(cityValue) {
      for (const province of this.provinces) {
        if (this.cities[province.value]?.some(c => c.value === cityValue)) {
          return province.value;
        }
      }
      return '';
    },
    handleCitiesChange() {  // 
      this.$nextTick(() => {
        this.syncRegionBlacklist();
      });
    },
    // 
    saveQRCode() {
      try {
        // a
        const downloadLink = document.createElement('a');
        downloadLink.href = this.qrCodeUrl;
        downloadLink.download = `channel_qrcode_${this.currentChannel.id || 'download'}.png`;
        
        // 
        document.body.appendChild(downloadLink);
        downloadLink.click();
        
        // DOM
        document.body.removeChild(downloadLink);
        
        // 
        this.$message && this.$message.success('');
      } catch (error) {
        console.error(':', error);
        this.$message && this.$message.error('');
      }
    },
    getHeaderIcon(header) {
      switch (header) {
        case 'ID':
          return 'fas fa-hashtag';
        case '':
          return 'fas fa-tag';
        case '':
          return 'fas fa-font';
        case '':
          return 'fas fa-fingerprint';
        case '':
          return 'fas fa-calendar-alt';
        case '':
          return 'fas fa-users';
        case '':
          return 'fas fa-money-bill-wave';
        case '':
          return 'fas fa-cog';
        case '':
          return 'fas fa-circle';
        default:
          return '';
      }
    },
    // 
    getChannelTypeIcon(type) {
      switch (type) {
        // ''  '' 
        case '':
          return 'fas fa-bolt'; // 
        case '':
          return 'fas fa-credit-card'; // 
        default:
          return 'fas fa-credit-card'; // 
      }
    },
    // 
    getChannelTypeBadgeClass(type) {
      switch (type) {
        case '':
          return 'type-badge-wechat';
        case '':
          return 'type-badge-alipay';
        case '':
          return 'type-badge-cloudpay';
        case '':
          return 'type-badge-unionpay';
        default:
          return '';
      }
    }
  },
  mounted() {
    // 
    this.checkMobile();
    window.addEventListener('resize', this.checkMobile);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.checkMobile);
  },
  async created() {
    this.loadChannels();
    this.fetchDomains();
    this.checkMobile();
    window.addEventListener('resize', this.checkMobile);
  },
  destroyed() {
    window.removeEventListener('resize', this.checkMobile);
  }
}
</script>

